import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import loadable from '@loadable/component';
import Box from '@mui/material/Box';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Seo from '../components/seo';

import { initializeApp } from 'firebase/app';
import {
  getFirestore,
  doc,
  setDoc,
  addDoc,
  collection
} from 'firebase/firestore';

const SectionMenu = loadable(() => import('../components/sections/menu'));
const SectionMainvisual = loadable(() =>
  import('../components/sections/mainvisual')
);
const SectionAbout = loadable(() => import('../components/sections/about'));
const SectionAccess = loadable(() => import('../components/sections/access'));
const SectionContact = loadable(() => import('../components/sections/contact'));

const theme = createTheme({
  palette: {
    primary: {
      main: '#b28223'
    },
    background: {
      default: 'transparent'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['Noto Serif JP', 'YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32,
      letterSpacing: 1.5
    },
    h5: {
      fontSize: 28
    }
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        root: {
          body: {
            backgroundColor: '#f00',
            color: '#0f0'
          }
        }
      }
    },
    MuiLink: {
      styleOverrides: {
        root: {
          color: 'inherit'
        }
      }
    }
  }
});

const IndexPage = () => {
  const firebaseConfig = {
    apiKey: 'AIzaSyBakeOGclhSzZXdiS_9YKbWmXWcA0h5JiM',
    authDomain: 'kotomusubi-4dcd9.firebaseapp.com',
    projectId: 'kotomusubi-4dcd9',
    storageBucket: 'kotomusubi-4dcd9.appspot.com',
    messagingSenderId: '522547548892',
    appId: '1:522547548892:web:e990b1fccc6ab26e4d1a19',
    measurementId: 'G-DJKG0G4399'
  };

  const app = initializeApp(firebaseConfig);
  const db = getFirestore(app);

  console.log(app);
  console.log(db);

  React.useEffect(() => {
    const f = async () => {
      const docRef = await addDoc(collection(db, 'ranking'), {
        name: 'Tokyo',
        country: 'Japan'
      });
    };

    f();
  }, []);

  const {
    site: {
      siteMetadata: { tel }
    }
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          tel
        }
      }
    }
  `);

  const duration = 1500;
  const delay = 250;

  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isEnded, setIsEnded] = React.useState(false);

  const onLoad = () => {
    setIsLoaded(true);
    setTimeout(() => {
      setIsEnded(true);
    }, duration + delay);
  };

  React.useEffect(() => {
    document.readyState === 'complete'
      ? onLoad()
      : window.addEventListener('load', onLoad);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {!isEnded && (
        <Box
          position='fixed'
          sx={{
            backgroundColor: '#000',
            transition: `opacity ${duration}ms ease-out ${delay}ms`,
            opacity: isLoaded ? 0 : 1,
            width: '100vw',
            height: '100vh',
            zIndex: 2000,
            pointerEvents: 'none'
          }}
        />
      )}
      <SectionMenu tel={tel} />
      <SectionMainvisual />
      <SectionAbout />
      <SectionAccess />
      <SectionContact tel={tel} />
    </ThemeProvider>
  );
};

export default IndexPage;

export const Head = () => {
  return <Seo />;
};
